import RenderNews from '../renderNews';


export default class LocalStorageClass {
  constructor() {
    this.popularArr = [];
    this.favoriteArr = [];
    this.read = [];
    this.id = '';
    this.imageURL = '';
    this.category = '';
    this.title = '';
    this.desc = '';
    this.date = '';
    this.url = '';
  }

    onAddToFavoriteClick(e) {
      if (
        !e.target.parentNode.classList.contains('item-news__add-to-favorite')
      ) {
        return;
      }
      e.target // toggle class
        .closest('.item-news__add-to-favorite')
        .classList.toggle('hidden-span');
      const choosenNews = e.target.closest('.list-news__item'); // 
      this.id = choosenNews.dataset.id; // id 
      const choosenNewsMarkup = `<li class="list-news__item popular-news__item"data-id=${this.id}>${choosenNews.innerHTML}</li>`; // 
      const chooseNewsObj = {
        //  id  
        id: this.id,
        markup: choosenNewsMarkup,
      };

      if (localStorage.getItem('favorite')) {
        //    LS    favorite ?
        //   LS   favorite  =>
        const localStorageFavoriteData = localStorage.getItem('favorite'); //   LS
        this.favoriteArr = JSON.parse(localStorageFavoriteData) || []; //   ( )


        const doesHaveElement = this.favoriteArr.findIndex(
          news => news.id === this.id
        ); //      ,    id
        console.log(doesHaveElement);
        if (doesHaveElement < 0) {
          //  
          this.favoriteArr.push(chooseNewsObj);
          localStorage.setItem('favorite', JSON.stringify(this.favoriteArr));
        } else {
          //  
          this.favoriteArr.splice(doesHaveElement, 1);
          localStorage.setItem('favorite', JSON.stringify(this.favoriteArr));
        }

      } else {
        //   LS   favorite
        this.favoriteArr.push(chooseNewsObj);
        localStorage.setItem('favorite', JSON.stringify(this.favoriteArr));
        
      }
  }
  
  onReadMoreClick(e) {
    if (!e.target.classList.contains('item-news__info-link')) {
      return;
    }

    

    const choosenNews = e.target.closest('.list-news__item'); // 
    this.id = choosenNews.dataset.id; // id 
    const choosenNewsMarkup = `<li class="list-news__item popular-news__item"data-id=${this.id}>${choosenNews.innerHTML}</li>`; // 
    const chooseNewsObj = {
      //  id  
      id: this.id,
      markup: choosenNewsMarkup,
    };

    if (localStorage.getItem('read')) {
      //    LS    read ?
      //   LS   read  =>
      const localStorageReadData = localStorage.getItem('read'); //   LS
      this.read = JSON.parse(localStorageReadData) || []; //   ( )

      const doesHaveElement = this.read.findIndex(
        news => news.id === this.id
      ); //      ,    id
      console.log(doesHaveElement);
      if (doesHaveElement < 0) {
        //  
        this.read.push(chooseNewsObj);
        localStorage.setItem('read', JSON.stringify(this.read));
      } 
    } else {
      //   LS   read
      this.read.push(chooseNewsObj);
      localStorage.setItem('read', JSON.stringify(this.read));
    }
  }
  
  

}
